import * as React from "react";
import { NavLink } from "react-router-dom";
import { getPilots } from "./pilots-data";

export const PilotsStat = (props) => {
  const { year } = props.match.params;

  if (!year) location.hash = "/drivers/2020";
  const activeStyle = {
    color: "rgb(71, 61, 61)",
    textShadow: "3px 2px 1px rgb(71, 61, 61)",
  };

  const pilotView = (pilot) => {
    const { driver } = pilot;

    return (
      <div className="stat-p-row" key={driver.number}>
        <div>{driver.number}</div>
        <div className="stat-p-wider">
          <a href={driver.link}>{driver.name}</a>
        </div>
        <div>{pilot.season}</div>
        <div className="stat-p-wider">{pilot.team}</div>
        <div>{pilot.points}</div>
        <div>{pilot.poles}</div>
        <div>{pilot.bestLap}</div>
        <div>{pilot.podiums}</div>
        <div>{pilot.laps}</div>
      </div>
    );
  };

  return (
    <div id="pilots">
      <div id="year-p">
        <div className="year-stats">–°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –ø–æ –ø–∏–ª–æ—Ç–∞–º</div>
        <div className="year-pilots">
          <NavLink to="/drivers/2020" activeStyle={activeStyle}>
            2020
          </NavLink>
        </div>
        <div className="year-pilots">
          <NavLink to="/drivers/2019" activeStyle={activeStyle}>
            2019
          </NavLink>
        </div>
        <div className="year-pilots">
          <NavLink to="/drivers/2018" activeStyle={activeStyle}>
            2018
          </NavLink>
        </div>
      </div>

      <div id="stat-p">
        <div>‚Ññ</div>
        <div className="stat-p-wider">–ü–∏–ª–æ—Ç</div>
        <div>–°–µ–∑–æ–Ω–æ–≤</div>
        <div className="stat-p-wider">–ö–æ–º–∞–Ω–¥–∞</div>
        <div>–û—á–∫–∏</div>
        <div>–ü–æ—É–ª—ã</div>
        <div>–õ—É—á—à–∏–π –∫—Ä—É–≥</div>
        <div>–ü–æ–¥–∏—É–º—ã</div>
        <div>–ö—Ä—É–≥–æ–≤</div>
      </div>

      {getPilots(year).map((pilot) => pilotView(pilot))}
    </div>
  );
};
